<template>
    <v-container class="pa-0">
        <!-- Header -->
        <PDHeader></PDHeader>

        <!-- Main -->
        <v-main class="pt-0">
            <v-container>
                <!-- 透析類別切換 -->
                <v-btn class="CAPD-btn" @click="isShow = 'CAPD'">CAPD</v-btn>
                <v-btn class="APD-btn" @click="isShow = 'APD'">APD</v-btn>

                <!-- CPAD Input -->
                <v-card class="mt-5 border border-black" v-if="isShow === 'CAPD'">
                    <v-form>
                        <v-row class="pt-5 px-5">
                            <v-col class="d-flex justify-start">
                                <span>▼</span>
                                <Datepicker v-model="CAPD_form_data.date" class="date-picker" />
                            </v-col>
                        </v-row>


                        <v-row class="pt-5 px-5">
                            <v-col cols="6"> <v-text-field v-model="CAPD_form_data.blood_pressure_h" label="血壓(收縮壓)"
                                    type="number" variant="outlined" /></v-col>
                            <v-col cols="6"> <v-text-field v-model="CAPD_form_data.blood_pressure_l" label="血壓(舒張壓)"
                                    type="number" variant="outlined" /></v-col>
                        </v-row>

                        <v-row class="pt-5 px-5">
                            <v-col cols="12"> <v-text-field v-model="CAPD_form_data.weight" label="體重" type="number"
                                    variant="outlined" /></v-col>
                        </v-row>



                        <v-row class="pa-5 mb-5" v-for="(n, i) in 5" :key="i">
                            <v-col><span>第{{ n }}次</span></v-col>
                            <v-container class="d-flex align-center pb-0">

                                <v-col class="px-0 pr-2" cols="6"><v-text-field v-model="CAPD_form_data[`injection${n}`]"
                                        :label="`注入量`" type="number" variant="outlined" /></v-col>
                                <v-col class="px-0 pl-2" cols="6"><v-text-field v-model="CAPD_form_data[`dehydration${n}`]"
                                        :label="`排出量`" type="number" variant="outlined" /></v-col>
                            </v-container>
                            <v-col>
                                <v-select :items="concentration_list" label="透析液濃度"></v-select>
                                <v-select :items="drainage_time_list" label="引流時間"></v-select>
                                <v-select :items="CAPD_dehydration_state_list" label="排出藥水狀態"></v-select>
                            </v-col>

                        </v-row>

                        <v-row class="px-5 mb-10">
                            <v-container class="d-flex flex-column">
                                <span>血糖濃度:&nbsp;</span>

                                <v-container class="d-flex justify-space-between px-0">
                                    <v-col class="px-1 pl-0" cols="4"><v-text-field v-model="CAPD_form_data.m_glucose"
                                            label="早" type="number" variant="outlined" /></v-col>
                                    <v-col class="px-1" cols="4"><v-text-field v-model="CAPD_form_data.a_glucose" label="中"
                                            type="number" variant="outlined" /></v-col>
                                    <v-col class="px-1 pr-0" cols="4"><v-text-field v-model="CAPD_form_data.n_glucose"
                                            label="晚" type="number" variant="outlined" /></v-col>
                                </v-container>

                            </v-container>
                        </v-row>

                        <v-btn class="CAPD-save-btn" @click="save_CAPD">存檔</v-btn>
                    </v-form>
                </v-card>

                <!-- APD Input -->
                <v-card class="mt-5 border border-black" v-else-if="isShow === 'APD'">
                    <v-form>
                        <v-row class="pt-5 px-5">
                            <v-col class="d-flex justify-start">
                                <span>▼</span>
                                <Datepicker v-model="APD_form_data.date" />
                            </v-col>
                        </v-row>

                        <v-row class="pt-5 px-5">
                            <v-col cols="6"> <v-text-field label="血壓(收縮壓)" type="number" variant="outlined"
                                    v-model="APD_form_data.blood_pressure_h" /></v-col>
                            <v-col cols="6"> <v-text-field label="血壓(舒張壓)" type="number" variant="outlined"
                                    v-model="APD_form_data.blood_pressure_l" /></v-col>
                        </v-row>

                        <v-row class="pt-5 px-5">
                            <v-col cols="12"> <v-text-field label="體重" type="number" variant="outlined"
                                    v-model="APD_form_data.weight" /></v-col>
                        </v-row>

                        <v-row class="px-5">
                            <v-col cols="6"> <v-text-field label="CAPD引流量" type="number" variant="outlined"
                                    v-model="APD_form_data.CAPD_drainage" /></v-col>
                            <v-col cols="6"> <v-text-field label="CAPD注入量" type="number" variant="outlined"
                                    v-model="APD_form_data.CAPD_injection" /></v-col>
                        </v-row>

                        <v-row class="px-5">
                            <v-col cols="12"><v-text-field type="number" variant="outlined" label="0週期引流量"
                                    v-model="APD_form_data.zero_drainage" /></v-col>
                        </v-row>

                        <v-row class="px-5">
                            <v-col cols="12"> <v-text-field label="APD顯示總脫水量" type="number" variant="outlined"
                                    v-model="APD_form_data.APD_dehydration" /></v-col>
                        </v-row>

                        <v-row class="px-5">
                            <v-col cols="12"> <v-text-field label="最末袋注入" type="number" variant="outlined"
                                    v-model="APD_form_data.last_injection" /></v-col>
                        </v-row>

                        <v-row class="px-5">
                            <v-col cols="12"> <v-text-field label="平均留置時間(小時:分鐘)" type="text" variant="outlined"
                                    v-model="APD_form_data.average_time" /></v-col>
                        </v-row>




                        <v-row class="px-5 mb-5">
                            <v-col cols="12"><v-select v-model="APD_form_data.heating_bag" label="加溫袋" :items="heating_bag_list"></v-select></v-col>
                            <v-col cols="12"><v-select v-model="APD_form_data.refill_bag" label="補充袋" :items="refill_bag_list"></v-select></v-col>
                            <v-col cols="12"><v-select v-model="APD_form_data.last_bag" label="最末袋" :items="last_bag_list"></v-select></v-col>
                            <v-col cols="12"><v-select v-model="APD_form_data.machine_warning" label="機器警示次數" :items="machine_warning_list"></v-select></v-col>
                            <v-col cols="12"><v-select v-model="APD_form_data.dehydration_state" label="排出藥水狀態"
                                    :items="APD_dehydration_state_list"></v-select></v-col>
                        </v-row>
                        <v-row class="px-5 mb-10">
                            <v-col cols="12">
                                <v-textarea label="備註"></v-textarea>
                            </v-col>
                        </v-row>
                        <v-btn class="APD-save-btn" @click="save_APD">存檔</v-btn>
                    </v-form>

                </v-card>
            </v-container>

        </v-main>

        <!-- Footer -->
        <PDFooter></PDFooter>
    </v-container>
</template>

<script setup>
// components
import PDHeader from '@/components/PD-Header.vue';
import PDFooter from '@/components/PD-Footer.vue';
import { useStore } from 'vuex';
import { ref } from 'vue';
import router from '@/router';
// DatePicker套件
import Datepicker from 'vue3-datepicker'


// data
const store_list = useStore().state.peritoneal_list;
const isShow = ref('CAPD')
const section_list = useStore().state.peritoneal_section_list;

const concentration_list = section_list.filter(function (item) {
    return item.type == 'concentration'
}).map(function (item) {
    return item.section;
});

const drainage_time_list = section_list.filter(function (item) {
    return item.type == 'drainage_time'
}).map(function (item) {
    return item.section;
});

const CAPD_dehydration_state_list = section_list.filter(function (item) {
    return item.type == 'CAPD_dehydration_state'
}).map(function (item) {
    return item.section;
})

const heating_bag_list = section_list.filter(function (item) {
    return item.type == 'heating_bag'
}).map(function (item) {
    return item.section;
})

const refill_bag_list = section_list.filter(function (item) {
    return item.type == 'refill_bag'
}).map(function (item) {
    return item.section;
});

const last_bag_list = section_list.filter(function (item) {
    return item.type == 'last_bag'
}).map(function (item) {
    return item.section;
});

const machine_warning_list = section_list.filter(function (item) {
    return item.type == 'machine_warning'
}).map(function (item) {
    return item.section;
});

const APD_dehydration_state_list = section_list.filter(function (item) {
    return item.type == 'APD_dehydration_state'
}).map(function (item) {
    return item.section;
});







const CAPD_form_data = ref({
    date: ref(new Date()),
    blood_pressure_h: ref(0),
    blood_pressure_l: ref(0),
    weight: ref(0),
    total_dehydration: ref(0),
    m_glucose: ref(0),
    a_glucose: ref(0),
    n_glucose: ref(0),
    num: ref(store_list.length + 1),
    type: 'CAPD',
    injection1: ref(0),
    dehydration1: ref(0),
    injection2: ref(0),
    dehydration2: ref(0),
    injection3: ref(0),
    dehydration3: ref(0),
    injection4: ref(0),
    dehydration4: ref(0),
    injection5: ref(0),
    dehydration5: ref(0)

});


const save_CAPD = () => {
    let a = CAPD_form_data.value
    a.total_dehydration = (Number(a.dehydration1) + Number(a.dehydration2) + Number(a.dehydration3) + Number(a.dehydration4) + Number(a.dehydration5)) - (Number(a.injection1) + Number(a.injection2) + Number(a.injection3) + Number(a.injection4) + Number(a.injection5))
    store_list.push(a)
    router.push('/peritonealdialysis');

}

const APD_form_data = ref({

    date: ref(new Date()),
    blood_pressure_h: ref(0),
    blood_pressure_l: ref(0),
    weight: ref(0),
    num: ref(store_list.length + 1),
    type: 'APD',
    zero_drainage: ref(0),
    last_injection: ref(0),
    APD_dehydration: ref(0),
    CAPD_drainage: ref(0),
    CAPD_injection: ref(0),
    CAPD_dehydration: ref(0),
    total_dehydration: ref(0),
    average_time: ref(''),
    m_glucose: ref(0),
    a_glucose: ref(0),
    n_glucose: ref(0),
    heating_bag: ref(''),
    refill_bag: ref(''),
    last_bag: ref(''),
    dehydration_state: ref(''),
    machine_warning: ref(''),
})
const save_APD = () => {
    let b = APD_form_data.value;
    b.CAPD_dehydration=Number(b.last_injection)-Number(b.CAPD_drainage);
    b.total_dehydration = (Number(b.zero_drainage)-Number(b.last_injection))+Number(b.CAPD_drainage)-Number(b.CAPD_injection)+Number(b.APD_dehydration);
    store_list.push(b);
    router.push('/peritonealdialysis');
}


</script>

<style lang="scss" scoped>
.CAPD-btn {
    width: 50%;
}

.APD-btn {
    width: 50%;
}

.CAPD-save-btn {

    position: absolute;
    bottom: 0;
    right: 0;
    background-color: lightgray;
}

.APD-save-btn {

    position: absolute;
    bottom: 0;
    right: 0;
    background-color: lightgray;
}
</style>